#hd {
  border-bottom: 1px solid #e8e8e8; }
  #hd.fix-header {
    width: 100%;
    background-color: #FFFFFF;
    background-color: rgba(255, 255, 255, 0.95);
    position: fixed;
    top: 0;
    left: 0;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
    z-index: 6; }
  #hd.no-fix {
    width: 100%;
    background-color: #fff;
    overflow: hidden; }
  #hd.show {
    -webkit-transform: translateX(200px);
    -ms-transform: translateX(200px);
    transform: translateX(200px); }

.nv-toolbar {
  width: 100%;
  height: 44px;
  display: -webkit-box;
  -webkit-box-align: center; }
  .nv-toolbar .toolbar-nav {
    width: 49px;
    height: 44px;
    position: absolute;
    background: url("../assets/images/components/nav_icon.png") no-repeat center center;
    background-size: 19px 16px;
    margin: 0;
    z-index: 1;
    top: 0;
    left: 0; }
  .nv-toolbar > span {
    display: block;
    text-align: center;
    height: 100%;
    line-height: 44px;
    font-size: 16px;
    width: 100%;
    position: relative;
    z-index: 0; }
  .nv-toolbar .num {
    background-color: #80bd01;
    color: #fff;
    width: 20px;
    height: 20px;
    line-height: 20px;
    vertical-align: middle;
    text-align: center;
    border-radius: 50%;
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 10; }
  .nv-toolbar .add-icon {
    color: #42b983;
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 10;
    padding: 5px 15px;
    border-radius: 5px; }

.scroll-hide {
  height: 100%;
  overflow: hidden; }



