@charset "UTF-8";
/*侧边栏*/
.nav-list {
  position: fixed;
  top: 0;
  bottom: 0;
  left: -200px;
  width: 200px;
  background-color: #fff;
  color: #313131;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  z-index: 99; }
  .nav-list.show {
    -webkit-transform: translateX(200px);
    -ms-transform: translateX(200px);
    transform: translateX(200px); }

/*侧边栏列表*/
.list-ul {
  margin: 0 24px;
  border-top: 1px solid #d4d4d4;
  overflow: hidden;
  padding-top: 2.5vh; }
  .list-ul li {
    font-size: 14px;
    font-weight: 200;
    padding: 2.5vh 0;
    text-align: left;
    text-indent: 1px;
    line-height: 15px;
    color: #7f8c8d; }
    .list-ul li:last-child {
      margin-bottom: 50px; }
    .list-ul li:before {
      color: #2c3e50; }
  .list-ul .line {
    border-top: 1px solid #d4d4d4; }
  .list-ul a {
    display: block;
    color: #313131; }



