.add-container {
  margin-top: 50px; }
  .add-container .line {
    padding: 10px 15px;
    border-bottom: solid 1px #d4d4d4; }
    .add-container .line .add-btn {
      color: #fff;
      background-color: #80bd01;
      padding: 5px 15px;
      border-radius: 5px; }
    .add-container .line .add-tab {
      width: calc(100% - 140px);
      padding: 5px;
      font-size: 16px;
      background: transparent; }
      .add-container .line .add-tab :after {
        content: 'xe60e'; }
    .add-container .line .add-title {
      font-size: 16px;
      border: none;
      width: 100%;
      background: transparent;
      height: 25px; }
    .add-container .line .err {
      border: solid 1px red; }
  .add-container .add-content {
    margin: 15px 2%;
    width: 96%;
    border-color: #d4d4d4;
    color: #000; }
  .add-container .err {
    border: solid 1px red; }



