@charset "UTF-8";
/*侧边栏用户信息区域*/
.user-info {
  padding: 15px;
  font-size: 15px;
  color: #313131; }

/*未登录*/
.login-no {
  overflow: hidden;
  margin: 8px 9px; }
  .login-no > li {
    float: right;
    height: 24px;
    line-height: 24px;
    padding-left: 34px;
    position: relative; }
    .login-no > li:before {
      width: 24px;
      height: 24px;
      content: '';
      position: absolute;
      left: 0;
      top: 0; }
  .login-no .login {
    float: left; }
    .login-no .login:before {
      background: url("../assets/images/components/login_icon.png") no-repeat left center;
      background-size: 24px 24px; }

/*已登录*/
.login-yes {
  height: 100%;
  background: url("../assets/images/components/go_next_icon.png") no-repeat right center;
  background-size: 6px 10px;
  overflow: hidden;
  position: relative; }
  .login-yes .avertar {
    width: 40px;
    height: 40px;
    background: url("../assets/images/components/user.png") no-repeat center center;
    background-size: 40px 40px;
    border-radius: 20px;
    overflow: hidden;
    float: left; }
    .login-yes .avertar > img {
      width: 40px;
      height: 40px;
      display: block; }
  .login-yes .info {
    margin-left: 10px;
    overflow: hidden;
    float: left; }
  .login-yes p {
    width: 85px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 12px;
    line-height: 12px;
    line-height: 40px; }
    .login-yes p.lh20 {
      line-height: 20px; }
  .login-yes:after {
    display: block;
    background: url("../assets/images/components/go_icon.png") no-repeat center right;
    background-size: 7px 7px; }



